svg {
  position: absolute;
  top: 0;
}

.content {
  position: absolute;
  top: 12rem;
  right: 2.5rem;
  width: 65%;
}

.content * {
  position: relative;
  right: 5rem;
  top: 4rem;
  margin-bottom: 1rem;
  text-align: center;
}

.content h1 {
  font-family: 'Berkshire Swash', cursive;
  color: var(--white);
  font-size: 3rem !important;
  line-height: 1;
  margin-bottom: 2rem;
}

.content h2 {
  font-size: 2rem !important;
  font-weight: 500;
  line-height: 1;
}

.byline {
  position: relative;
  text-align: center;
  font-size: 54px
  
}

.date {
  position: relative;
  top: 30rem;
  bottom: 2.5rem;
  font-size: 0.75rem;
}

.bold {
  font-weight: 500;
}

.heart {
  fill: red;
  position: relative;
  top: 10px;
  width: 70px;
  animation: pulse 1s ease infinite;
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.3); }
  100% { transform: scale(1); }
}
